<template>
  <div id="app" :class={filter:ifit}>
    <v-header :seller='seller' v-on:cfilter="ifilter"></v-header>
    <div class="tabs">
      <div class="tabs-item">
        <a v-link="{ path:'/goods' }"> 商品 </a>
      </div>
      <div class="tabs-item">
        <a v-link="{ path:'/ratings' }"> 评价 </a>
      </div>
      <div class="tabs-item">
        <a v-link="{ path:'/seller' }">商家</a>
      </div>
    </div>
    <router-view :seller='seller' keep-alive></router-view>
  </div>
</template>
<script type="text/ecmascript-6">
require('./common/css/nav_tabs.css');
import header from './components/header/header.vue';
import sellers from './exportData/seller'
export default {
  name:"app",
  components: { 'v-header': header },
  data() {
      return{
        seller:{},
        ifit:false
      }
  },
  ready(){
  },
  methods:{
      ifilter:function(ifit){
          this.ifit=true;
      }
  },
  created() {
    this.seller=sellers
  }
}
</script>
<style>
</style>



