// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import goods from './router/goods/goods.vue'
import seller from './router/seller/seller.vue'
import rating from './router/ratings/rating.vue'
Vue.use(VueRouter)

/* eslint-disable no-new */
let app = Vue.extend(App)
let router = new VueRouter({
  linkActiveClass:'active' //设置路由a标签激活时添加的样式
})
router.map({
  '/goods': {
    component: goods
  },
  '/ratings': {
    component: rating
  },
  '/seller': {
    component: seller
  }
})
router.start(app, '#app')
router.go("/goods")




