<template>
  <div v-el:rb class="ratings_wrap">
    <div>
      <div class="rating_head">
        <div class="rating_count"><p>{{seller.score}}</p>
          <p>综合评分</p>
          <p>高于周边商家{{seller.rankRate}}%</p></div>
        <div class="service_rating">
          <p>服务态度:

            <star :score="seller.serviceScore" :size=2></star>
            <span>{{seller.serviceScore}}</span></p>
          <p>食物评价:

            <star :score="seller.foodScore" :size=2></star>
            <span>{{seller.foodScore}}</span></p>
          <p>送达时间:<span>{{seller.deliveryTime}}分钟</span></p>
        </div>
      </div>
      <ratings :ratingtext="ratingtext" :onlytext="onlytext"></ratings>
      <div class="rating_box">
        <ul class="shoprating_lists">
          <li v-for="item in shopRatings" v-show="commentCho(item.rateType,item.text)">
            <img :src="item.avatar">
            <div class="ra_right">
              <p>{{item.username}}<span>{{item.rateTime}}</span></p>
              <p>
                <star :score="item.score" :size=3></star>
                <span v-show="item.deliveryTime">{{item.deliveryTime}}分钟送达</span></p>
              <p>{{item.text}}</p>
              <p><span :class="{'highO':item.rateType==0}" class="high"></span>
                <label v-show="item.recommend">
                  <a v-for="food in item.recommend">{{ food }}</a>
                </label>
              </p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import star from "../../components/star.vue"
  import BScroll from "better-scroll"
  import ratings from "../../components/ratings/ratings.vue"
  import dataRatings from "../../exportData/ratings"
  export default {
    name: '',
    components: {star, ratings},
    data () {
      return {
        ratingtext: [
          {
            text: "全部",
            type: 0,
            num: 12
          },
          {
            text: "满意",
            type: 1,
            num: 12
          },
          {
            text: "不满意",
            type: 2,
            num: 12
          }
        ],
        onlytext: false,
        shopRatings: [],
        oCommentType: 0,
        commentType: 0
      }
    },
    props: {
      seller: {}
    },
    methods: {
      commentCho(type, text){
        switch (this.commentType) {
          case 0:
            if (this.onlytext === false)
              return true
            else if (!text)
              return false
            else
              return true;
            break;
          case 1:
            if (this.onlytext === false) {
              if (type === 0) {
                return true
              }
              else {
                return false
              }
            }
            else {
              if (type === 0 && text) {
                return true
              }
              else {
                return false
              }
            }
            break;
          case 2:
            if (this.onlytext === false) {
              if (type === 1) {
                return true
              }
              else {
                return false
              }
            }
            else {
              if (type === 1 && text) {
                return true
              }
              else {
                return false
              }
            }
            break;
          case 3:
            if (!this.onlytext) {
              if ((this.oCommentType === 0) || (this.oCommentType === 1 && type === 0) || (this.oCommentType === 2 && type === 1)) {
                return true
              }
              else
                return false
            }
            else {
              if ((this.oCommentType === 0 && text) || (this.oCommentType === 1 && text && type == 0) || (this.oCommentType === 2 && text && type === 1)) {
                return true
              }
              else
                return false
            }
            break;
        }
      },
    },
    created(){
        this.shopRatings = dataRatings;
        console.log("a")
        this.$nextTick(() => {
          this.ratingScroll = new BScroll(this.$els.rb, {
            click: true
          })
        })
        let all = 0;
        let highOpin = 0;
        let lowOpin = 0;
        for (let i = 0; i < this.shopRatings.length; i++) {
          all++
          if (this.shopRatings[i].rateType === 0) {
            highOpin++
          }
          else {
            lowOpin++
          }
        }
        for (let i = 0; i < this.ratingtext.length; i++) {
          if (this.ratingtext[i].type == 0) {
            this.ratingtext[i].num = all
          }
          else if (this.ratingtext[i].type == 1) {
            this.ratingtext[i].num = highOpin
          }
          else {
            this.ratingtext[i].num = lowOpin
          }
        }

    },
    events: {
      "getComType"(type, ot){
        if (this.commentType != 3)
          this.oCommentType = this.commentType;
        this.onlytext = ot;
        this.commentType = type
        this.$nextTick(() => {
          this.ratingScroll.refresh()
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .ratings_wrap{
    position: absolute;
    top: 3.5rem;
    overflow: hidden;
    bottom:0;
  }
  .rating_head {
    overflow: hidden;
    margin-top: 0.38rem;
    padding-bottom: 0.7rem;
    margin-bottom: 0.36rem;
    background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
    background-size: 100% auto;
  }

  .rating_head .rating_count {
    width: 2.76rem;
    float: left;
    text-align: center;
    border-right: 1px solid #e6e7e8;
    height: 1.4rem;
  }

  .rating_head .service_rating {
    width: 4.32rem;
    float: right;
    height: 1.4rem;
  }

  .rating_count p:first-child {
    height: 0.58rem;
    line-height: 0.58rem;
    font-size: 0.48rem;
    color: #ff9900;
  }

  .rating_count p:nth-child(2) {
    height: 0.37rem;
    line-height: 0.37rem;
    font-size: 0.24rem;
    color: #07111b;
  }

  .rating_count p:nth-child(3) {
    height: 0.33rem;
    line-height: 0.33rem;
    font-size: 0.2rem;
    color: #93999f;
  }

  .service_rating p {
    overflow: hidden;
    height: 0.36rem;
    margin-bottom: 0.1rem;
    line-height: 0.36rem;
    font-size: 0.24rem;
    color: #07111b;
  }

  .service_rating p span {
    color: #ff9900;
    vertical-align: middle;
  }

  .service_rating p:last-child span {
    color: #93999f;
    margin-left: 0.2rem;
    vertical-align: top;
  }

  .service_rating div {
    display: inline-block;
    margin: 0 0.2rem;
  }

  .rating_box {
    width: 100%;
    overflow: hidden;
  }

  .shoprating_lists {
    padding: 0 0.35rem;
  }

  .shoprating_lists li {
    padding: 0.35rem 0;
    border-bottom: 1px solid #e6e7e8;
    overflow: hidden;
  }
  .shoprating_lists li:last-child{
    margin-bottom: 10px;
  }
  .shoprating_lists img {
    width: 0.57rem;
    height: auto;;
    float: left;
    border-radius: 0.285rem;
    margin-right: 0.2rem;
  }

  .shoprating_lists .ra_right {
    width: 6rem;
    float: right;
  }

  .ra_right p {
    overflow: hidden;
    font-size: 0.24rem;
    line-height: 0.36rem;
    color: #07111b;
  }

  .ra_right p:first-child {
    font-size: .2rem;
  }

  .ra_right span {
    font-size: 0.2rem;
    vertical-align: middle;
    color: #93999f;
  }

  .ra_right p:first-child span {
    float: right;
    vertical-align: top;
  }

  .ra_right a {
    font-size: 0.18rem;
    display: inline-block;
    padding: 0.02rem 0.12rem;
    border: 0.01px solid #e6e7e8;
    color: #93999f;
    margin-right: 0.12rem;
    margin-top: 0.1rem;
  }


</style>



