<template>
  <div class="shop_detail" v-show="detailType&&menuList.length" transition="slide" v-el:shopdetail>
    <p class="shop_title">购物车<span @click="clearfood">清空</span></p>
    <ul>
      <li v-for="item in menuList" v-show="item.count">
        <span>{{item.name}}</span>
        <control-menu :foods="item"></control-menu>
        <label>¥{{item.price}}</label>
      </li>
    </ul>
  </div>
  <div class="shopcar_box" @click="detail_show($event)">
    <div class="shopcar_left">
      <span attr-quantity="0" class="cartview-aZYUv" :class="carhasNoCho"></span>
      <label class="menuCount" v-show="menuCount">{{menuCount}}</label>
    </div>
    <div class="shopcar_right">
      <div class="allPri" :class="hasChoMenu">¥{{allPrice}}</div>
      <div class="delPri">另需配送费{{seller.deliveryPrice}}元</div>
      <div class="minPri" :class="meetSet">{{deliDes}}</div>
    </div>
    <div class="ball_box">
    <div class="shopcar_ball" transition="drop" v-for="ball in balls" v-show="ball.showBall">
      <div class="inner"></div>
    </div>
    </div>
  </div>
</template>

<script>
  import controlMenu from "../../components/goods/controlMenu.vue"
  import BScroll from "better-scroll"
  export default {
    props: {
      "menuList": {
        type: Array,
        default(){
          return []
        }
      },
      "seller": {
        type: Object
      }
    },
    data () {
      return {
        balls: [
          {
            showBall: false,
          },
          {
            showBall: false,
          },
          {
            showBall: false,
          },
          {
            showBall: false,
          },
          {
            showBall: false,
          }
        ],
        dropBall: [],
        detailType:false
      }
    },
    components:{
      controlMenu
    },
    methods: {
      detail_show(){
        if(!this.detailType){
        if(this.menuCount)
        this.detailType=!this.detailType
        }
        else
        this.detailType=!this.detailType
      },
      drop(el){
        for (let i = 0; i < this.balls.length; i++) {
          var ball = this.balls[i]
          if (!ball.showBall) {
            ball.showBall = true;
            ball.el = el;
            this.dropBall.push(ball)
            return
          }
        }
      },
      clearfood(event){
        this.menuList.forEach((item)=>{
          item.count=0
        })
        this.detailType=false
      }
    },
    transitions: {
      drop: {
        beforeEnter(el){
          let count = this.balls.length;
          while (count--) {
            let ball=this.balls[count]
            if (ball.showBall) {
              let rect = ball.el.getBoundingClientRect();
              let x = rect.left-window.innerWidth/750*60;
              let y =-( window.innerHeight - rect.top-window.innerWidth/750*40);
              el.style.webkitTransform = "translate3d(0,"+y+"px,0)"
              el.style.transform = "translate3d(0,"+y+"px,0)"
              el.getElementsByClassName("inner")[0].style.webkitTransform = "translate3D("+x+"px,0,0)"
              el.getElementsByClassName("inner")[0].style.transform = "translate3D("+x+"px,0,0)"
            }
          }
        },
        enter(el){
          let rf = el.offsetHeight;
          this.$nextTick(() => {
            el.style.webkitTransform = "translate3d(0,0,0)"
            el.style.transform = "translate3d(0,0,0)"
            el.getElementsByClassName("inner")[0].style.webkitTransform = "translate3D(0,0,0)"
            el.getElementsByClassName("inner")[0].style.transform = "translate3D(0,0,0)"

          })
        },
        afterEnter(el){
          let ball = this.dropBall.shift()
          if (ball) {
            ball.showBall = false;
            el.style.display = "none"
          }
        }
      }
    },
    computed: {
      allPrice(){
        var allPri = 0;
        this.menuList.forEach((item) => {
          allPri += item.price * item.count
        })
        return allPri
      },
      hasChoMenu(){
        if (this.allPrice) {
          return "hasChoMenu"
        }
      },
      carhasNoCho(){
        if (!this.allPrice) {
          return "cartview-XwoZE"
        }
      },
      deliDes(){
        if (!this.allPrice) {
          return "¥" + this.seller.minPrice + "起送"
        }
        else if (this.allPrice < this.seller.minPrice) {
          return "还差" + (this.seller.minPrice - this.allPrice) + "起送"
        }
        else if (this.allPrice >= this.seller.minPrice) {
          return "立即结算"
        }
      },
      meetSet(){
        if (this.allPrice >= this.seller.minPrice) {
          return "meetSet"
        }
      },
      menuCount(){
        var allCount = 0;
        this.menuList.forEach((item) => {
          allCount += item.count
        })
        return allCount
      }
    },
    created(){
      this.$nextTick(() => {
        this.shopdeScroll=new BScroll(this.$els.shopdetail,
        {
          click:true
        })
      })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .shopcar_box {
    height: 0.96rem;
    padding-top: 0.2rem;
    position: fixed;
    display: flex;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index:20;
  }

  .shopcar_left {
    flex: 0 0 1.6rem;
    width: 1.6rem;
    background: #141d27;
  }

  .shopcar_right {
    flex: 1;
    background: #141d27;
    overflow: hidden;
  }

  .shopcar_right div {
    float: left;
    height: 0.96rem;
    line-height: 0.96rem;
  }

  div.allPri {
    width: 0.7rem;
    font-size: 0.32rem;
    color: rgba(255, 255, 255, 0.4);
    font-weight: bold;
  }

  div.delPri {
    margin-left: 0.2rem;
    width: 1.75rem;
    font-size: 0.24rem;
    color: rgba(255, 255, 255, 0.4);
  }

  div.minPri {
    width: 2.1rem;
    float: right;
    text-align: center;
    color: rgba(255, 255, 255, 0.4);
    font-size: 0.24rem;
    font-weight: bold;
    background: #2b333b;
  }

  .cartview-aZYUv.cartview-XwoZE {
    background-image: -webkit-radial-gradient(circle, #363636 .626667rem, #444 0);
    background-image: radial-gradient(circle, #363636 .626667rem, #444 0);
  }

  .cartview-aZYUv {
    position: absolute;
    left: .26rem;
    top: 0rem;
    width: 1.05rem;
    height: 1.05rem;
    box-sizing: border-box;
    border-radius: 100%;
    background-color: #3190e8;
    border: .12rem solid #444;
    box-shadow: 0 -.08rem .053333rem 0 rgba(0, 0, 0, .1);
    will-change: transform;
  }

  .cartview-aZYUv.cartview-XwoZE:before {
    background-image: url(data:image/svg+xml;base64,)
  }

  .cartview-aZYUv:before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url(data:image/svg+xml;base64,) center center no-repeat;
    background-size: .433333rem;
    content: "";
  }

  :before {
    box-sizing: inherit;
  }

  .menuCount {
    display: block;
    width: 0.46rem;
    height: 0.31rem;
    -webkit-border-radius: 0.2rem;
    -moz-border-radius: 0.2rem;
    border-radius: 0.2rem;
    text-align: center;
    color: #fff;
    line-height: 0.31rem;
    position: absolute;
    font-size: 0.2rem;
    top: 0;
    left: 0.88rem;
    background: red;
  }

  .cartview-aZYUv.cartview-XwoZE:after {
    visibility: hidden;
  }

  div.hasChoMenu {
    color: #fff;
  }

  div.meetSet {
    background-color: #4cd964;
    color: #fff;
  }

  .shopcar_ball {
    position: absolute;
    width: 0.3rem;
    left: 0.65rem;
    top: 0.4rem;
    z-index:10000;
    border-radius: 0.15rem;
    transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
    height: 0.3rem;
  }

  .shopcar_ball .inner {
    height: 0.3rem;
    border-radius: 0.15rem;
    background: #000;
    width: 0.3rem;
    transition: all 0.4s linear;
  }
  .shop_detail{
    position:fixed;
    bottom:0.96rem;
    left:0;
    width:100%;
    z-index:19;
    height:auto;
  }
  .shop_detail .shop_title{
    height:0.78rem;
    line-height:0.78rem;
    overflow: hidden;
    background: #f4f5f7;
    padding-left:0.37rem;
    font-size:0.28rem;
    color: rgb(7,17,27);
    border-bottom:0.02rem solid rgba(7,17,27,0.1)
  }
  .shop_title span{
    float: right;
    margin-right: 0.31rem;
    font-size:0.24rem;
    color: rgb(0,160,220);
  }
  .shop_detail ul{
    padding:0 0.36rem;
    background: #fff;
  }
  .shop_detail li{
    height:0.96rem;
    line-height:0.96rem;
    overflow: hidden;
    border-bottom:0.01rem solid rgba(7,17,27,0.1);
  }
  .shop_detail li span{
    font-size:0.28rem;
    color: rgb(7,17,27);
  }
  .shop_detail label{
    float: right;
    margin-right: 0.25rem;
    font-size:0.2rem;
    font-weight:bold;
    color: rgb(240,20,20);
  }
  .shop_detail .add_box{
    float: right;
    position: relative;
    margin-right: 0.02rem;
    right:0;
    bottom:0;
    padding-right: 0.01rem;
    margin-top: 0.28rem;
  }
  .slide-transition{
    transform:translate3d(0,0,0);
    transition:all 0.4s;
  }
  .slide-enter,.slide-leave{
    transform:translate3d(0,100%,0);
  }
</style>



