<template>
  <ul class="rating_choose">
    <li v-for="item in ratingtext" :class="['active'+item.type,{'active':liClass==item.type}]" @click="chooseComment(item.type,$event)" >
      {{item.text}}<span>{{item.num}}</span>
    </li>
  </ul>
  <p class="onSee" :class="{'onlyChoose':onlytext==true}" @click="chooseComment(3,$event)">只看有内容的评价</p>
</template>

<script>
  export default {
    data () {
      return {
          liClass:0
      }
    },
    props: {
      ratingtext: {
        type: Array
      },
      onlytext:{

      }
    },
    computed: {},
    methods: {
      chooseComment(type,event){
        if(type!=3){
            this.liClass=type
        }
          switch (type){
            case 3:
                this.onlytext=!this.onlytext
          }
          this.$dispatch("getComType",type,this.onlytext)

      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .rating_choose {
    overflow: hidden;
    margin: 0 0.36rem;
    padding-bottom: 0.36rem;
    border-bottom: 0.01rem solid #e6e7e8;
  }

  .rating_choose li {
    float: left;
    margin-right: 0.16rem;
    width: 1.2rem;
    height: 0.63rem;
    line-height: 0.63rem;
    text-align: center;
    font-size: 0.24rem;
    color: #fff;
    text-align: center;
    background: #00a0dc;
    color: #4d555d;
  }

  .rating_choose span {
    font-size: 0.2rem;
    vertical-align: bottom;
  }

  .rating_choose .active1 {
    background: #ccecf8;
  }

  .rating_choose .active2 {
    background: #e9ebec;
  }
  .rating_choose .active{
    color: #fff;
  }

  .onSee {
    height: 0.96rem;
    line-height: 0.96rem;
    border-bottom: #e6e7e8 0.02rem solid;
    color: #93999f;
    font-size: 0.24rem;
    padding-left: 0.9rem;
    background: url("../../common/img/choose_03.jpg") center left no-repeat;
    background-size: 0.4rem auto;
    background-position: 0.4rem 0.29rem;
  }

  .onlyChoose {
    background: url("../../common/img/choose2_03.jpg") center left no-repeat;
    background-size: 0.4rem auto;
    background-position: 0.4rem 0.29rem;
  }
</style>



