<template>
  <div class="goods_wrapper">
    <div class="menu" v-el:menu-wrapper>
      <ul>
        <li v-for="item in goods" :class="{'current':currentPos===$index}" @click="chooseMenu($index,$event)">
          <span>{{item.name}}</span>
        </li>
      </ul>
    </div>
    <div class="content" v-el:foods-wrapper>
      <ul class="big_item">
        <li v-for="item in goods" class="big_item_li big_item_li_hook" >
          <h1>{{item.name}}</h1>
          <ul class="small_item">
            <li class="small_item_li" v-for="foods in item.foods"  @click="selectThis(foods,$event)">
              <img :src="foods.icon">
              <div class="small_item_right">
                <strong>{{foods.name}}</strong>
                <p v-show="foods.description">{{foods.description}}</p>
                <p><span>月售{{foods.sellCount}}份</span><span>好评率:{{foods.rating}}%</span></p>
                <p><span class="new_pri">￥{{foods.price}}</span><span class="old_pri" v-show="foods.oldPrice">￥{{foods.oldPrice}}</span>
                </p>
                <control-menu :foods="foods"></control-menu>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
  <shopcar :menu-list="menuList" :seller="seller" v-ref:shopcar></shopcar>
  <food-detail :food="selectFood" :detailshow="detailshow" v-ref:food-detail></food-detail>
</template>

<script>
  import BScroll from "better-scroll"
  import shopcar from "../../components/goods/shopcar.vue"
  import controlMenu from "../../components/goods/controlMenu.vue"
  import foodDetail from "../../components/goods/foodDetail.vue"
  import dataGoods from "../../exportData/goods"
  export default {
    props: {
      "seller": {
        type: Object
      }
    },
    components:{
        shopcar,controlMenu,foodDetail
    },
    data () {
      return {
        goods: [],
        heightArr:[],
        posi:0,
        menuList:[],
        detailshow:false,
        selectFood:{}
      }
    },
    computed:{
      currentPos(){
          for(let i=0;i<this.heightArr.length;i++)
          {
              if(this.posi>=this.heightArr[i]&&this.posi<this.heightArr[i+1])
                  return i
          }
      },
      menuList(){
          let foods=[];
          this.goods.forEach((item)=>{
              item.foods.forEach((food)=>{
                if(food.count)
                {
                  foods.push(food)
                }
              })
          })
          return foods
      }
    },
    created(){
        this.goods = dataGoods
        this.$nextTick(() => {
          this._scrollFoods()
          this._calculHeight()
          this.foodsscroll.on("scroll",(pos)=>{
              this.posi=Math.floor(Math.abs(pos.y))
          })
        })
    },
    methods:{
        _scrollFoods(){
          this.menuscroll=new BScroll(this.$els.menuWrapper,{
            click:true
          })
          this.foodsscroll=new BScroll(this.$els.foodsWrapper,{
            probeType:3,
            click:true
          })
        },
      _drop(target){
        this.$nextTick(()=>{
          this.$refs.shopcar.drop(target)
        })
      },
      selectThis(food,event){
        if (!event._constructed) {//如果不存在这个属性,则不执行下面的函数
          return;
        }
        this.selectFood=food;
        this.detailshow=true
        this.$refs.foodDetail.detail_shows()
      },
      _clear(){
        this.goods.forEach((item)=>{
                      item.foods.forEach((food)=>{
                        if(food.count)
                        {
                          delete food.count
                        }
                      })
                  })
      },
      _calculHeight(){
        var el=this.$els.foodsWrapper;
        var elLi=el.getElementsByClassName("big_item_li_hook")
        var heig=0
        this.heightArr.push(0)
        var op=elLi[1].getElementsByTagName("p")[0]
        for(let i=0;i<elLi.length;i++)
        {
          heig+=elLi[i].offsetHeight;
          this.heightArr.push(heig)
        }
      },
      chooseMenu(index,event){
        if(!event._constructed){//如果不存在这个属性,则不执行下面的函数
          return;
        }
        var el=this.$els.foodsWrapper;
        var elLi=el.getElementsByClassName("big_item_li_hook")
        this.foodsscroll.scrollToElement(elLi[index],300)
      }
    },
    events:{
        "drop"(target){
            this._drop(target)
        },
        "clearCount"(target){
          this._clear()
        }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .goods_wrapper {
    display: flex;
    position: absolute;
    top: 3.48rem;
    left: 0;
    width: 100%;
    bottom: 0.96rem;
    overflow: hidden;
  }

  .menu {
    flex: 0 0 1.6rem;
    background: #f3f5f7;
    width: 1.12rem;
  }

  .content {
    flex: 1;
  }

  .menu li {
    height: 1.08rem;
    line-height: 0.28rem;
    font-size: 0.24rem;
    padding: 0 0.24rem;
    color: #333;
    display: table;
    width: 1.12rem;
    border-bottom: 0.01rem solid rgba(7, 17, 21, 0.1);
  }
  .menu li:last-child{
    border: none;
  }
  .menu li.current{
    background:#fff;
    font-size:0.24rem;
    border-bottom:none;
    height: 1.09rem;
  }
  .menu span {
    display: table-cell;
    vertical-align: middle;
  }
  .big_item h1{
    height:0.52rem;
    line-height:0.52rem;
    padding-left: 0.24rem;
    background: #f3f5f7;
    font-size:0.24rem;
    color: rgb(147,153,159);
    border-left: 0.04rem solid #d9dde1;
  }
  .small_item{
    padding:0 0.36rem;
  }
  .small_item_li{
    padding: 0.362rem 0;
    border-bottom:1px solid #d9dde1;
    position: relative;
    overflow: hidden;
  }
  .small_item_li:last-child{
    border:none;
  }
  .small_item_li img{
    float:left;
    width:1.14rem;
    height:1.14rem;
    margin-right: 0.2rem;
  }
  .small_item_li strong{
    display: block;
    height:0.36rem;
    line-height:0.36rem;
    font-size:0.28rem;
    color: rgb(7,17,27);
  }
  .small_item_li p{
    height:0.37rem;
    line-height:0.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width:3.1rem;
    color: rgb(147,153,159);
    font-size:0.2rem;
  }
  .small_item_li span{
    margin-right: 0.24rem;
  }
  .new_pri{
    font-size:0.28rem;
    color: rgb(240,20,20);
    font-weight:bold;
  }
  .old_pri{
    font-size:0.2rem;
    font-weight:bold;
    vertical-align: top;
    color: rgb(147,153,159);
  }
</style>




