<template>
  <div class="add_box">
    <a class="remove" v-show="foods.count" @click.stop.prevent="decreaseGoods($event)" transition="move"></a>
    <label v-show="foods.count">{{foods.count}}</label>
    <a class="add" @click.stop.prevent="addGoods($event)"></a>
  </div>
</template>

<script>
  import Vue from "vue"
  export default {
    props: {
      foods: {
        type: Object
      }
    },
    methods: {
      addGoods(event){
        if (!event._constructed) {//如果不存在这个属性,则不执行下面的函数
          return;
        }
        if (!this.foods.count) {
          Vue.set(this.foods, "count", 1)
        }
        else {
          this.foods.count++
        }
        this.$dispatch("drop",event.target)
      },
      decreaseGoods(event){
        if (!event._constructed) {//如果不存在这个属性,则不执行下面的函数
          return;
        }
        if (this.foods.count > 0)
          this.foods.count--
      }
    }
  }
</script>
<style>
  .add_box {
    position: absolute;
    width: 1.36rem;
    height: 0.4rem;
    line-height: 0.4rem;
    right: 0.04rem;
    bottom: 0.3rem
  }

  .add_box a {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 0.4rem;
    transition: all 0.4s linear;
    height: 0.4rem;
    background: url("../../common/img/add_03.jpg") left top no-repeat;
    background-size: 100% auto;
    margin-right: 0.24rem;
  }

  .add_box a:last-child {
    background: url("../../common/img/add_07.jpg") left top no-repeat;
    background-size: 100% auto;
    margin-left: 0.24rem;
    margin-right: 0;
    left: 0.75rem;
  }

  .add_box label {
    display: block;
    width: 0.56rem;
    height: 0.4rem;
    line-height: 0.4rem;
    position: absolute;
    left: 0.4rem;
    top: 0;
    text-align: center;
    color: rgb(147, 153, 159);
  }

  .move-transition {
    transform: translate3D(0, 0, 0);
    transform: rotate(0)
  }

  .move-enter, .move-leave {
    transform: translate3D(-0.96rem, 0, 0);
    transform: rotate(180deg)
  }
</style>



