<template>
  <div class="seller_wrap" v-el:seller-wrap>
    <div>
      <div class="seller_title">
        <h2>{{seller.name}}</h2>
        <p>
          <star :score="seller.score" :size=3></star>
          (661)月售690单

        </p>
        <span v-el:shoucan class="shouspan" :class="isshou" @click="changeshou">点击收藏</span>
      </div>
      <div class="seller_msg">
        <ul>
          <li>
            起送价:<br><span>{{seller.minPrice}}</span>元


          </li>
          <li>
            商家配送:<br><span>{{seller.deliveryPrice}}</span>元


          </li>
          <li>
            平均配送时间:<br><span>{{seller.deliveryTime}}</span>分钟


          </li>
        </ul>
      </div>
      <div class="activity">
        <h2 class="com_h2">公告与活动</h2>
        <p class="sellerIntro">{{seller.bulletin}}</p>
        <ul class="busin_com_ul">
          <li v-for="item in seller.supports">
            {{item.description}}


          </li>
        </ul>
      </div>
      <div class="pics">
        <h2 class="com_h2">商家实景</h2>
        <div v-el:busin-pics>
          <ul>
            <li v-for="item in seller.pics">
              <img :src="item">
            </li>
          </ul>
        </div>
      </div>
      <div class="busin_info">
        <h2 class="com_h2">商家信息</h2>
        <ul class="busin_com_ul">
          <li v-for="item in seller.infos">{{item}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import star from "../../components/star.vue"
  import BScroll from "better-scroll"
  export default {
    name: '',
    props: {
      seller: {}
    },
    watch: {
      "seller"(){
        this.sellerInit()
        this.picInit()
      }
    },
    ready(){
      this.$nextTick(function () {
        if (this.seller.pics.length) {
          this.sellerInit()
          this.picInit()
        }
      })
    },
    methods: {
      sellerInit(){
        this.$nextTick(() => {
          this.sellerScroll = new BScroll(this.$els.sellerWrap, {
            click: true,
            probeType: 3
          })
        })
      },
      picInit(){
        this.$nextTick(() => {
          let PICUL = this.$els.businPics.getElementsByTagName("ul")[0]
          let PICLI = PICUL.getElementsByTagName("li")
          let re=document.documentElement.clientWidth/7.5;
          for(let i=0;i<PICLI.length;i++){
            PICLI[i].style.height=1.8*re+"px"
          }
          PICUL.style.width = Number(Number(PICLI.length * 2.4) + Number((PICLI.length - 1) * 0.12))*re+"px"
          this.picScroll = new BScroll(this.$els.businPics, {
            scrollX: true,
            probeType: 3
          })
        })
      },
      changeshou(){
        this.shoucanType = !this.shoucanType
        if (this.shoucanType) {
          this.$els.shoucan.innerHTML = "已收藏"
        }
        else {
          this.$els.shoucan.innerHTML = "点击收藏"
        }
      }
    },
    computed: {
      isshou(){
        if (this.shoucanType) {
          return "sellershou"
        }
        else {
          return "sellernoshou"
        }
      }
    },
    data () {
      return {
        shoucanType: false
      }
    },
    components: {star}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .seller_wrap {
    position: absolute;
    top: 3.48rem;
    width: 100%;
    bottom: 0;
    overflow: hidden;
  }

  .seller_title {
    padding: 0.23rem;
    overflow: hidden;
    position: relative;
    border-bottom: 1px solid #e6e7e8;
  }

  .seller_title h2 {
    font-size: 0.28rem;
    height: 0.5rem;
    line-height: 0.5rem;
    margin-bottom: 0.1rem;
  }

  .star_box_3 {
    vertical-align: bottom;
  }

  .shouspan {
    position: absolute;
    width: 1rem;
    height: 0.4rem;
    padding-top: 0.47rem;
    text-align: center;
    font-size: 0.2rem;
    color: #666;
    right: 0.23rem;
    top: 0.33rem;
    background: url("../../common/img/heart2_03.png") top center no-repeat;
    background-size: 0.4rem 0.37rem;
  }

  .sellershou {
    background: url("../../common/img/heart_03.png") top center no-repeat;
    background-size: 0.4rem 0.37rem;

  }

  .sellernoshou {
    background: url("../../common/img/heart2_03.png") top center no-repeat;
    background-size: 0.4rem 0.37rem;

  }

  .seller_msg {
    padding: 0.23rem;
    padding-bottom: 0.59rem;
    background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
    background-size: 100% auto;
  }

  .seller_msg ul {
    display: flex;
  }

  .seller_msg li {
    flex: 1;
    text-align: center;
    border-right: 0.01rem solid #e6e7e8;
    color: #93999f;
    font-size: 0.2rem;
    line-height: 0.4rem
  }

  .seller_msg li:last-child {
    border-right: none;
  }

  .seller_msg span {
    font-size: 0.48rem;
    color: #07111b;
  }

  .activity {
    padding: 0.12rem 0.36rem;
  }

  h2.com_h2 {
    height: 0.6rem;
    color: #07111b;
    font-size: 0.28rem;
    line-height: 0.6rem;
  }

  .activity {
    background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
    padding-bottom: 0.36rem;
    background-size: 100% auto;
  }

  .activity p {
    font-size: 0.24rem;
    color: #f01414;
    line-height: 0.4rem;
    padding-left: 0.32rem;
    margin-bottom: 0.3rem;
  }

  .busin_com_ul {
    color: #07111b;
    font-size: 0.24rem
  }

  .busin_com_ul li {
    padding-left: 0.7rem;
    border-top: 0.01rem solid #e6e7e8;
    height: 0.96rem;
    line-height: 0.96rem;
    background-size: 0.31rem 0.32rem;
    background-repeat: no-repeat;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background-position: 0.24rem center;
  }

  .activity li:nth-child(1) {
    background-image: url("../../common/img/seller_Act_07.png");
  }

  .activity li:nth-child(2) {
    background-image: url("../../common/img/seller_Act_11.png");
  }

  .activity li:nth-child(3) {
    background-image: url("../../common/img/seller_Act_11.png");
  }

  .activity li:nth-child(4) {
    background-image: url("../../common/img/seller_Act_13.png");
  }

  .activity li:nth-child(5) {
    background-image: url("../../common/img/seller_Act_13.png");
  }

  .activity li:nth-child(6) {
    background-image: url("../../common/img/seller_Act_15.png");
  }

  .activity li:nth-child(6) {
    background-image: url("../../common/img/seller_act_17.jpg");
  }

  .pics {
    padding-top: 0.35rem;
    padding-left: 0.23rem;
    background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
    padding-bottom: 0.36rem;
    background-size: 100% auto;
  }

  .busin_info {
    padding: 0.23rem 0.35rem;
    padding-bottom: 0;
  }

  .busin_info li {
    padding-left: 0.22rem;
  }

  .pics div {
    width: 100%;
    overflow: hidden;
  }

  .pics ul {
    overflow: hidden;
    margin-bottom: 0.35rem;
  }

  .pics li {
    width: 2.4rem;
    height: 1.8rem;
    float: left;
    margin-right: 0.12rem;
  }

  .pics li:last-child {
    margin-right: 0;
  }

  .pics img {
    width: 100%;
    height:100%
  }
</style>



