<template>
  <div v-show="detailshow" class="fooddetail" transition="transmove" v-el:detail-show>
    <div class="detail_box">
      <span class="detail_close" @click="detail_hide($event)">&lt;</span>
      <div class="food_image"><img :src="food.image"></div>
      <div class="food_deta_box">
        <h1 class="food_name">{{food.name}}</h1>
        <p class="food_com"><span>月售{{food.sellCount}}份</span>好评率{{food.rating}}</p>
        <p><span class="new_pri">￥{{food.price}}</span><span class="old_pri"
                                                             v-show="food.oldPrice">￥{{food.oldPrice}}</span></p>
        <a class="add_car" v-show="!food.count" @click="addCar($event)" transition="opac">加入购物车</a>
        <control-menu :foods="food"></control-menu>
      </div>
      <div class="food_intro" v-show="food.info">
        <h2>商品介绍</h2>
        <p>{{food.info}}</p>
      </div>
      <div class="food_rating">
        <h2>商品评价</h2>
        <ratings :ratingtext="ratingtext" :onlytext="onlytext"></ratings>
        <ul class="rating_lists">
          <li v-for="item in food.ratings" v-show="commentCho(item.rateType,item.text)" >
            <p>2017-2-5  20:20 <span>{{item.username}}<img :src="item.avatar"></span></p>
            <p class="rating_text"><span :class="{'highO':item.rateType==0}" class="high"></span>{{item.text}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from "better-scroll"
  import Vue from "vue"
  import controlMenu from "../../components/goods/controlMenu.vue"
  import ratings from "../../components/ratings/ratings.vue"
  export default {
    data () {
      return {
        ratingtext: [
          {
            text: "全部",
            type: 0,
            num: 12
          },
          {
            text: "推荐",
            type: 1,
            num: 12
          },
          {
            text: "吐槽",
            type: 2,
            num: 12
          }
        ],
        onlytext: false,
        commentType: 0,
        oCommentType: 0
      }
    },
    props: ["food", "detailshow"],
    created(){
    },
    components: {
      controlMenu, ratings
    },
    methods: {
      commentCho(type, text){
        switch (this.commentType) {
          case 0:
            if (this.onlytext === false)
              return true
            else if (!text)
              return false
            else
              return true;
            break;
          case 1:
            if (this.onlytext === false) {
              if (type === 0) {
                return true
              }
              else {
                return false
              }
            }
            else {
              if (type === 0 && text) {
                return true
              }
              else {
                return false
              }
            }
            break;
          case 2:
            if (this.onlytext === false) {
              if (type === 1) {
                return true
              }
              else {
                return false
              }
            }
            else {
              if (type === 1 && text) {
                return true
              }
              else {
                return false
              }
            }
            break;
          case 3:
            if (!this.onlytext) {
              if ((this.oCommentType === 0) || (this.oCommentType === 1 && type === 0) || (this.oCommentType === 2 && type === 1)) {
                return true
              }
              else
                return false
            }
            else {
              if ((this.oCommentType === 0 && text) || (this.oCommentType === 1 && text && type == 0) || (this.oCommentType === 2 && text && type === 1)) {
                return true
              }
              else
                return false
            }
            break;
        }
      },
      addCar(event){
        if (!event._constructed) {//如果不存在这个属性,则不执行下面的函数
          return;
        }
        Vue.set(this.food, "count", 1)
        this.$dispatch("drop", event.target)
      },
      detail_hide(event){
        if (!event._constructed) {//如果不存在这个属性,则不执行下面的函数
          return;
        }
        this.detailshow = false
      },
      detail_shows(){
        this.detailshow = true
        this.$nextTick(() => {
          if (!this.detailScroll) {
            this.detailScroll = new BScroll(this.$els.detailShow, {
              click: true,
              probeType: 3,
            })
          }
          else {
            this.detailScroll.refresh()
          }

          let all = 0;
          let highOpin = 0;
          let lowOpin = 0;
          for (let i = 0; i < this.food.ratings.length; i++) {
            all++
            if (this.food.ratings[i].rateType === 0) {
              highOpin++
            }
            else {
              lowOpin++
            }
          }
          for (let i = 0; i < this.ratingtext.length; i++) {
            if (this.ratingtext[i].type == 0) {
              this.ratingtext[i].num = all
            }
            else if (this.ratingtext[i].type == 1) {
              this.ratingtext[i].num = highOpin
            }
            else {
              this.ratingtext[i].num = lowOpin
            }
          }
        })
      }
    },
    events: {
      "getComType"(type, ot){
        if (this.commentType != 3)
          this.oCommentType = this.commentType;
        this.onlytext = ot;
        this.commentType = type
        this.$nextTick(() => {
            this.detailScroll.refresh()
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .fooddetail {
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 7.5rem;
    bottom: 0.96rem;
    background: #fff;
    overflow: hidden;
  }

  .food_image {
    width: 100%;
    padding-top: 100%;
    position: relative;
    margin-bottom: 0.28rem;
  }

  .food_image img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .food_deta_box {
    height: 1.89rem;
    position: relative;
    padding: 0 0.35rem;
    padding-bottom: 0.36rem;
    background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
    background-size: 100%;
  }

  .fooddetail h1 {
    height: 0.45rem;
    line-height: 0.45rem;
    font-size: 0.28rem;
    font-weight: 700;
    color: rgb(7, 17, 27);
  }

  .fooddetail p.food_com {
    height: 0.3rem;
    line-height: 0.3rem;
    font-size: 0.2rem;
    color: rgb(147, 153, 159);
    margin-bottom: 0.35rem;
  }

  p.food_com span {
    margin-right: 0.2rem;
  }

  .add_car {
    position: absolute;
    width: 1.46rem;
    height: 0.48rem;
    border-radius: 0.24rem;
    display: block;
    line-height: 0.48rem;
    text-align: center;
    color: #fff;
    background: rgb(0, 160, 220);
    right: 0.36rem;
    bottom: 0.8rem;
    z-index: 10;
  }

  .fooddetail .add_box {
    position: absolute;
    right: 0.45rem;
    bottom: 0.8rem;
  }

  .opac-transition {
    opacity: 1;
    transition: 0.4s all;
  }

  .opac-enter, .opac-leave {
    opacity: 0;
  }

  .transmove-transition {
    left: 0;
    transition: 0.5s all
  }

  .transmove-enter, .transmove-leave {
    left: 100%;
  }

  .detail_close {
    position: absolute;
    display: block;
    width: 0.4rem;
    height: 0.4rem;
    top: 0.4rem;
    left: 0.2rem;
    font-size: 0.45rem;
    color: #fff;
    transform: scaleY(1.4);
    z-index: 10;
    font-weight: bold;
  }

  .food_intro {
    padding: 0.3rem 0.36rem;
    padding-bottom: 0.72rem;
    background: url("../../common/img/bottom_02.jpg") bottom left no-repeat;
    background-size: 100%;
  }

  .food_intro h2, .food_rating h2 {
    height: 0.46rem;
    line-height: 0.46rem;
    font-size: 0.3rem;
    color: #000;
    font-weight: normal;
  }

  .food_rating h2 {
    margin: 0.26rem 0.36rem;
  }

  .food_intro p {
    line-height: 0.48rem;
    color: rgb(77, 85, 93);
    font-size: 0.24rem;
  }

  .rating_lists {
    padding: 0 0.35rem;
  }

  .rating_text {
    height: 0.25rem;
  }

  span.high {
    background: url("../../common/img/finger_03.jpg") left center no-repeat;
    display: inline-block;
    vertical-align: bottom;
    height: 0.2rem;
    width: 0.23rem;
    margin-right: 0.2rem;
    -webkit-background-size: 100% auto;
    background-size: 100% auto;
  }

  span.highO {
    background: url("../../common/img/finger_07.jpg") left center no-repeat;
    -webkit-background-size: 100% auto;
    background-size: 100% auto;
  }

  .rating_lists li {
    padding: 0.3rem 0;
    border-bottom: 1px solid #e6e7e8;
  }

  .rating_lists p:first-child {
    overflow: hidden;
    margin-bottom: 0.18rem;
  }

  .rating_lists p:first-child span {
    float: right;
  }

  .rating_lists p:first-child img {
    margin: 0 0.27rem;
    vertical-align: bottom;
  }

  .rating_lists li p:first-child {
    height: 0.3rem;
    line-height: 0.3rem;
    font-size: 0.2rem;
    color: #93999f;
  }

  .rating_lists img {
    width: 0.23rem;
    height: auto;
  }
</style>



