<template>
  <div class="header">
    <div class="avatar">
      <img :src="seller.avatar">
    </div>
    <div class="header_right">
      <div class="seller_name">
        <span><img src="../../common/img/brand@3x.png"> </span><span>{{seller.name}}</span>
      </div>
      <div class="dispatch_type">
        <span>{{seller.description}}</span>/<span>{{seller.deliveryTime}}分钟送达</span>
      </div>
      <div class="supports" v-if="seller.supports">
        <span class="icon"><img src="../../common/img/decrease_2@3x.png"> </span>
        <span>{{seller.supports[0].description}}</span>
        <label v-on:click="change_filter" >{{seller.supports.length}}个></label>
      </div>
      <div class="bulletin">
        {{seller.bulletin}}
      </div>
    </div>
  </div>
  <div class="info" v-show="info_show">
    <div class="info_main">
      <h1>{{seller.name}}</h1>
      <star :score=4.6 :size=1></star>
      <h2>优惠信息</h2>
      <ul class="support_deta">
        <li  v-for="item in seller.supports">
          <span><img src="../../common/img/decrease_2@3x.png"> </span>
          {{item.description}}
        </li>
      </ul>
      <h2>商家公告</h2>
      <p>{{seller.bulletin}}</p>
    </div>
    <span class="close" @click="close_info">X</span>
  </div>
</template>
<script>
  import star from "../star.vue"
  require("../../common/css/header.css")
  export default {
    name: '',
    data () {
      return {
        info_show: false,
        sell:{}
      }
    },
    methods: {
      change_filter: function () {
        this.info_show = true;
        this.$emit("cfilter", this.info_show)    //子向父级传递参数
      },
      close_info: function(){
        this.info_show = false;
      },
    },
    props: {
        "seller":{
            type:Object
        }
    },
    components:{ star }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



